<template>
  <div>
    <phone-status />
    <header-title>客户管理</header-title>
    <el-container>
      <el-header>
        <el-menu :default-active="activeIndex" active-text-color="#FFBE00" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item @click="bianse1" :style="{color:bian1}" index="1">本地客户</el-menu-item>
        <el-menu-item @click="bianse2" :style="{color:bian2}" index="2">准客户</el-menu-item>
        <el-menu-item @click="bianse3" :style="{color:bian3}" index="3">客户</el-menu-item>
        </el-menu>
      </el-header>

      <el-footer>
          <router-view/>
      </el-footer>
    </el-container>


  </div>
</template>

<script>
import PhoneStatus from "../view/header/PhoneStatus";
import HeaderTitle from "../view/header/HeaderTitle";

export default {
  name: "Clients",
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      bian1: '',
      bian2: '',
      bian3: '',
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    bianse1(){
      this.bian1 = 'black'
      this.bian2 = ''
      this.bian3 = ''
      this.$router.push('LocalCustomers')
    },
    bianse2(){
      this.bian1 = ''
      this.bian2 = 'black'
      this.bian3 = ''
      this.$router.push('PermitCustomers')
    },
    bianse3(){
      this.bian1 = ''
      this.bian2 = ''
      this.bian3 = 'black'
      this.$router.push('GeneralCustomer')
    },
  },
  components: {
    PhoneStatus,
    HeaderTitle,
  }
}
</script>

<style scoped>
.el-header, .el-footer {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-header{
  width: 398px;
}
.el-footer{
  margin-top: 0px;
  background-color: #ffffff;
}
.el-menu{
  width: 398px;
  margin-left: -20px;
}
.el-menu-item{
  text-align: center;
  margin-left: 40px;
}
span{
  font-size: 15px;
  font-weight: bolder;
  margin-top: -4px;
}


</style>



