import Vue from 'vue'
import Router from 'vue-router'
//客户列表页面
import Clients from "@/view/Clients";

//本地客户
import LocalCustomers from "@/view/customers/LocalCustomers";
//准客户
import PermitCustomers from "../view/customers/PermitCustomers";
//客户
import GeneralCustomer from "../view/customers/GeneralCustomer";
//新增客户页面
import NewlyIncreasedCustomer from "../view/customers/NewlyIncreasedCustomer";

//本地客户资料
import IocalityInfo from "../view/userinfo/IocalityInfo";
//准客户资料
import PermitInfo from "../view/userinfo/PermitInfo";
//客户资料
import GeneralInfo from "../view/userinfo/GeneralInfo";

//编辑客户资料
import IocalityUp from "../view/userinfo/IocalityUp";
//编辑准客户资料
import PermitUp from "../view/userinfo/PermitUp";
//编辑成功页面
import affirm from "../view/userinfo/affirm";


Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'clients',
      component: Clients,
      redirect: '/LocalCustomers',
      children: [
        {
          path: '/LocalCustomers',
          name: 'LocalCustomers',
          component: LocalCustomers,
        },
        {
          path: '/PermitCustomers',
          name: 'PermitCustomers',
          component: PermitCustomers,
        },
        {
          path: '/GeneralCustomer',
          name: 'GeneralCustomer',
          component: GeneralCustomer,
        },
      ]
    },
    {
      path: '/NewlyIncreasedCustomer',
      name: 'NewlyIncreasedCustomer',
      component: NewlyIncreasedCustomer,
    },
    {
      path: '/IocalityInfo',
      name: 'IocalityInfo',
      component: IocalityInfo
    },
    {
      path: '/PermitInfo',
      name: 'PermitInfo',
      component: PermitInfo
    },
    {
      path: '/GeneralInfo',
      name: 'GeneralInfo',
      component: GeneralInfo
    },{
      path: '/IocalityUp',
      name: 'IocalityUp',
      component: IocalityUp
    },
    {
      path: '/PermitUp',
      name: 'PermitUp',
      component: PermitUp
    },
    {
      path: '/affirm',
      name: 'affirm',
      component: affirm
    }
  ]
})



