<template>
  <div>
    <phone-status/>
    <header-title>客户资料</header-title>

    <div>
      <img src="../../assets/img/准客户资料/u162.png" style="margin-left: -37px;margin-top: 38px;position: absolute">
      <img src="../../assets/img/准客户资料/u164.png" style="margin-left: -24px;margin-top: 46px;position: absolute">
      <div class="jiben" style="position: absolute;height: 7px;margin-top: 107px;width: 398px;margin-left: 0px" >
      </div>
    </div>

    <!-- 这个from可有可无，我就没有删除，因为我直接把添加的表拿过来直接用了所以   -->
    <el-form   label-width="100px" class="demo-ruleForm">
      <el-form-item label="客户名称" >{{client.cname}}</el-form-item>
      <el-form-item label="投保意向" >{{client.intention}}</el-form-item>

      <div class="jiben"  >
        <span>|&nbsp;&nbsp;&nbsp;基本信息</span>
      </div>

      <el-form-item label="证件类型" >{{client.codeType}}</el-form-item>
      <el-form-item label="单位性质" >{{client.unitType}}</el-form-item>
      <el-form-item label="证件号码">{{client.codeId}}</el-form-item>
      <el-form-item label="单位总人数" >{{client.unitCount}}</el-form-item>
      <el-form-item label="预计投保人数" >{{client.insureCount}}</el-form-item>
      <el-form-item label="单位地址" >{{client.address}}</el-form-item>

    <!--  联系人信息  -->
     <div v-for="(linkmen,index) in client.tclientLinkmen" :key="index">
        <div class="jiben"   >
          <span v-if="index == 0"  style="margin-left: 10px; font-size: 10px; float: left">|&nbsp;&nbsp;&nbsp;主联系人信息</span>
          <span v-if="index != 0"  style="margin-left: 10px; font-size: 10px; float: left">|&nbsp;&nbsp;&nbsp;副联系人信息</span>
        </div>
        <el-form-item label="姓名"  style="margin-top: 20px" >{{linkmen.lname}}</el-form-item>
        <el-form-item label="手机" >{{linkmen.ltel}}</el-form-item>
        <el-form-item label="办公电话" >{{linkmen.officePhone}}</el-form-item>
        <el-form-item label="出生日期">{{linkmen.hiredate}}</el-form-item>
        <el-form-item label="部门"  style="margin-top: 20px" >{{linkmen.dept}}</el-form-item>
        <el-form-item label="职位"  style="margin-top: 20px" >{{linkmen.job}}</el-form-item>
        <el-form-item label="邮箱"  style="margin-top: 20px" >{{linkmen.email}}</el-form-item>
     </div>
    </el-form>
  </div>
</template>

<script>
import PhoneStatus from "../header/PhoneStatus";
import HeaderTitle from "../header/HeaderTitle";
export default {
  name: "GeneralInfo",
  data(){
    return{
      id : this.$route.query.id,
      client:{},
      flag:{},
    }
  },
  components: {
    PhoneStatus,
    HeaderTitle
  },
  methods: {
    show(){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/queryPreGrpList1',{params: {id: this.id}})
        .then(res=>{
          this.client = res.data
          console.log(this.client);
        })
    },
    bj(){

    },
    sc(id){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/deleteClient',{params: {id: id}})
        .then(res=>{
          this.flag = res.data
          if(this.flag.flag == "删除成功"){
            this.$router.push('LocalCustomers')
          }
        })
    }
  },
  mounted() {
    this.show()
  }
}
</script>

<style scoped>
form{
  margin-top: 120px;
  margin-left: -20px;
}
.el-input{
  width: 217px;
}
.el-form-item{
  margin-left: 10px;
}
.jiben{
  height: 30px;
  background-color: rgba(228, 228, 228, 1);
  line-height: 30px;
  margin-left: 20px;
}
.jiben span{
  margin-left: -300px;
  font-size: 10px;
}
</style>



