<style scoped>
.el-container{
  height: 43px;
}
.el-header {
  background-color: rgba(228, 228, 228, 1);
  color: #333;
  text-align: center;
  line-height: 60px;
  font-size: 14px;
  font-weight: bolder;
  width: 398px;
}
.el-icon-arrow-left{
  position: absolute;
  margin-left: -190px;
  margin-top: 15px;
}
p{
  margin-top: -9px;
}
</style>



