<template>
  <div>
    <search @item-click="searcher"/>
    <ul class="infinite-list"  v-for="clint in list">
      <li style="font-size: 17px;font-weight: bolder" @click="$router.push({name:'GeneralInfo',query: {id: clint.id}})">{{clint.cname}}</li>
      <li>联系人 ：{{clint.tclientLinkmen[0].lname }}</li>
      <li><i class="el-icon-phone"></i>{{clint.tclientLinkmen[0].ltel }}</li>
    </ul>
  </div>
</template>

<script>
import Search from "../header/Search2";
export default {
  name: "GeneralCustomer",
  data() {
    return {
      list:[],
    };
  },
  methods: {
    show(){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/queryPreGrpList?id=2')
        .then(res=>{
          this.list = res.data
          console.log(this.list);
        })
    },
    searcher(ss){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/seacher',{params:{flag:ss}})
        .then(res=>{
          this.list = res.data
          console.log(this.list);
        })
    }
  },
  mounted() {
    this.show();
  },
  components: {
    Search
  }
}
</script>

<style scoped>
ul{
  list-style: none;
  border-bottom: #ccc 1px solid;
  width: 358px;
  margin-left: -20px;
}
li{
  text-align: left;
  line-height: 35px;
}
</style>



