<script>
import { regionData, CodeToText } from 'element-china-area-data'
import PhoneStatus from "../header/PhoneStatus";
import HeaderTitle from "../header/HeaderTitle";
export default {
  name: "NewlyIncreasedCustomer",
  data() {
    return {
      // options: regionData,
      // selectedOptions: [],
      id : this.$route.params.id,
      options: regionData,
      selectedOptions: [],
      ruleForm: {
        id: 0,
        cname: '',
        intention: '',
        codeType: '',
        industryType: '',
        unitType: '',
        codeId: '',
        unitCount: 0,
        insureCount: 0,
        address: '',
        starLevel: '',
        tclientLinkmen: [
          {
            dept: '',
            email: '',
            flag: '',
            hiredate: '',
            job: '',
            lname: '',
            ltel: '',
            officePhone: '',
            tlid: ''
          }
        ],
      }
    }
  },
  methods: {
    show(){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/queryPreGrpList1',{params: {id: this.id}})
        .then(res=>{
          this.ruleForm = res.data
          console.log(this.ruleForm);
        })
    },
    handleChange(value) {
      var zz=''
      zz+=CodeToText[value[0]]
      zz+=CodeToText[value[1]]
      zz+=CodeToText[value[2]]
      this.ruleForm.address=zz
    },
    submitForm(formName) {
      //这是自带的判断
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.axios.post('http://192.168.43.73:7001/guangdamove/updateClient',this.$qs.stringify(this.ruleForm,{arrayFormat: 'indices', allowDots: true}))
            .then(res=>{
              if(res.data.flag == "修改成功"){
                this.$router.push({name:'affirm',params: {id: this.id}})
              }else{

              }
            })
        } else {
          console.log('error submit!!');
          return false;
        }
      })
    },
    addContact(){
      this.ruleForm.tclientLinkmen.push({
        dept: '',
        email: '',
        flag: '',
        hiredate: '',
        job: '',
        lname: '',
        ltel: '',
        officePhone: '',
        tlid: ''
      })
    },
    deleteContact(index){
      this.ruleForm.tclientLinkmen.splice(index,1)
    }
  },
  components: {
    PhoneStatus,
    HeaderTitle,
  },
  mounted() {
    this.show()
  }
}
</script>

<style scoped>
form{
  margin-top: 120px;
  margin-left: -20px;
}
.el-input{
  width: 217px;
}
.jiben{
  height: 30px;
  background-color: rgba(228, 228, 228, 1);
  line-height: 30px;
  margin-left: 20px;
}
.jiben span{
  margin-left: -300px;
  font-size: 10px;
}
.el-button{
  width: 300px;
  background-color: #FFBE00;
  border: 0;
  color: #333333;
  margin-left: -90px;
}
</style>



