<template>
  <div>
    <search @item-click="searcher"/>
    <ul class="infinite-list"  v-for="clint in list">
      <el-row>
        <el-button type="warning" size="mini" style="position: absolute;margin-left: 90px;" v-if="clint.unitCount%2!=0" plain>&nbsp;&nbsp;保护中&nbsp;&nbsp;</el-button>
        <el-button type="danger" size="mini" style="position: absolute;margin-left: 90px" v-if="clint.unitCount%2==0" plain>即将到期</el-button>
      </el-row>
      <li style="font-size: 17px;font-weight: bolder"  @click="$router.push({name:'PermitInfo',query: {id: clint.id}})">{{clint.cname}}</li>
      <li>联系人 ：{{clint.tclientLinkmen[0].lname }}</li>
      <li><i class="el-icon-phone"></i>{{clint.tclientLinkmen[0].ltel }}</li>
    </ul>
  </div>
</template>

<script>
import Search from "../header/Search2";
export default {
  name: "PermitCustomers",
  data() {
    return {
      list:[],
    };
  },
  methods: {
    show(){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/queryPreGrpList?id=3')
        .then(res=>{
          this.list = res.data
          console.log(this.list);
        })
    },
    searcher(ss){
      this.$axios.get('http://192.168.43.73:7001/guangdamove/seacher',{params:{flag:ss}})
        .then(res=>{
          this.list = res.data
          console.log(this.list);
        })
    }
  },
  mounted() {
    this.show();
  },
  components: {
    Search
  }
}
</script>

<style scoped>
ul{
  list-style: none;
  border-bottom: #ccc 1px solid;
  width: 358px;
  margin-left: -20px;
}
li{
  text-align: left;
  line-height: 35px;
}
.el-button--danger.is-plain {
  color: #F56C6C;
  background: #ffffff;
  border-color: #fbc4c4;
}
.el-button--warning.is-plain {
  color: #E6A23C;
  background: #ffffff;
  border-color: #f5dab1;
}
</style>



