<template>
  <div>
  <el-container>
    <el-header>
      <el-form  label-width="80px" >
          <el-input v-model="ss" prefix-icon=" " placeholder="请输入准客户名称/手机号/证件号码"></el-input>
        <el-form-item>
          <img src="../../assets/img/准客户列表/u46.png"/>
          <el-button type="primary" @click="onSubmit">搜索</el-button>
        </el-form-item>
      </el-form>
    </el-header>
  </el-container>
  </div>
</template>

<script>
export default {
name: "Search2",
  data() {
    return {
        ss: '',
    }
  },
  methods: {
    onSubmit() {
      //发射给父级
      this.$emit('item-click',this.ss)
    }
  }
}
</script>

<style scoped>
.el-container{
  height: 60px;
  width: 398px;
  background-color: rgba(228, 228, 228, 1);
  margin-left: -20px;
}
.el-header {
  background-color: rgba(228, 228, 228, 1);
  color: #333;
  text-align: center;
  font-size: 14px;
  font-weight: bolder;
}
.el-icon-arrow-left{
  position: absolute;
  margin-left: -190px;
  margin-top: 15px;
}
p{
  margin-top: -9px;
}
.el-input{
  margin-left: -70px;
  margin-top: 5px;
  width: 310px;
  height: 42px;
}
.el-button--primary{
  position: absolute;
  margin-left: 85px;
  margin-top: -50px;
  background-color: #ffffff;
  border: 0;
  color: #333333;
}
.el-button+.el-button{
  color: #333333;
  width: 378px;
  height: 35px;
  margin-left: -89px;
  margin-top: 10px;
}
img{
  width: 16px;
  height: 16px;
  position: absolute;
  margin-left: -220px;
  margin-top: -39px;
}
</style>



